// Keyframes for animations
@keyframes float {
  0% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0); }
}

@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4); }
  70% { box-shadow: 0 0 0 15px rgba(255, 255, 255, 0); }
  100% { box-shadow: 0 0 0 0 rgba(255, 255, 255, 0); }
}

@keyframes glow {
  0% { filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.7)); }
  50% { filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.9)); }
  100% { filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.7)); }
}

// Vertical card transition animations
@keyframes fadeInUp {
  0% { opacity: 0; transform: translateY(30px) scale(0.9); }
  100% { opacity: 1; transform: translateY(0) scale(1); }
}

@keyframes fadeInDown {
  0% { opacity: 0; transform: translateY(-30px) scale(0.9); }
  100% { opacity: 1; transform: translateY(0) scale(1); }
}

@keyframes fadeOut {
  0% { opacity: 1; transform: scale(1); }
  100% { opacity: 0; transform: scale(0.9); }
}

.capsule-cards-container {
  width: 100%;
  height: auto;
  min-height: 700px;
  padding: 20px 0;
  position: relative;
  overflow-x: hidden;
  overflow-y: visible;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 20;
  
  // Prevent horizontal scrolling on mobile
  @media (max-width: 768px) {
    width: 100%;
    max-width: 100vw;
    margin: 0 auto;
    padding: 40px 0 60px;
  }
  
  // Navigation buttons with improved style
  .nav-arrow {
    position: absolute;
    z-index: 100;
    background: #36caba;
    color: #000;
    border: none;
    border-radius: 8px;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 24px;
    font-weight: bold;
    opacity: 1;
    transition: all 0.2s ease;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);

    &:hover, &:active {
      background: #2ba89a;
      transform: translateY(-2px);
      box-shadow: 0 6px 15px rgba(0, 0, 0, 0.4);
    }

    // Position buttons on left and right sides of the card on mobile
    @media (max-width: 768px) {
      width: 50px;
      height: 50px;
      border-radius: 8px;
      top: 50%;
      transform: translateY(-50%);
      
      &.prev {
        left: 10px;
      }

      &.next {
        right: 10px;
      }
      
      span {
        line-height: 1;
        position: relative;
        font-size: 24px;
      }
    }
    
    // Keep horizontal navigation for desktop
    @media (min-width: 769px) {
      top: 50%;
      transform: translateY(-50%);
      
      &.prev {
        left: 15px;
      }

      &.next {
        right: 15px;
      }
      
      span {
        position: relative;
        top: -2px;
      }
    }
  }

  // Cards slider
  .cards-slider {
    position: relative;
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    perspective: 1000px;
    z-index: 25;

    &.mobile {
      width: 100%;
      overflow: visible;
      height: 600px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      
      // Vertical transitions
      &.swiping-left .capsule-card.active {
        animation: fadeInUp 0.5s ease-out;
      }
      
      &.swiping-right .capsule-card.active {
        animation: fadeInDown 0.5s ease-out;
      }
      
      .capsule-card {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) !important;
        margin: 0 auto;
        width: 85% !important;
        max-width: 320px;
        height: 550px !important;
        transition: transform 0.5s ease-out, opacity 0.5s ease-out !important;
        backface-visibility: hidden;
        will-change: transform, opacity;
        box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
        
        &:not(.active) {
          opacity: 0 !important;
          pointer-events: none;
        }
        
        &.active {
          transform: translate(-50%, -50%) scale(1) !important;
          opacity: 1 !important;
        }
      }
    }

    &.desktop {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
      perspective: none;
      height: auto;
      padding: 20px;
      width: 100%;
      max-width: 1200px;
      margin: 0 auto;
      margin-top: -60px; /* Move cards higher up on desktop */
      
      @media (min-width: 992px) {
        grid-template-columns: repeat(6, 1fr);
        margin-top: -80px; /* Move cards even higher on larger screens */
      }
      
      @media (max-width: 991px) and (min-width: 768px) {
        grid-template-columns: repeat(3, 1fr);
        margin-top: -40px; /* Slightly less on medium screens */
      }
    }

    .capsule-card {
      border-radius: 40px;
      background: var(--card-color, #333);
      overflow: hidden;
      box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
      transition: all 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
      position: absolute;
      cursor: pointer;
      will-change: transform, opacity;
      
      &.unavailable {
        opacity: 0.6;
        cursor: not-allowed;
        filter: grayscale(100%) brightness(0.5);
        
        &:hover {
          transform: none !important;
          box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3) !important;
          filter: grayscale(100%) brightness(0.5) !important;
        }
      }

      @media (max-width: 768px) {
        width: 280px;
        height: 550px;
        position: absolute;
        z-index: 30;
        pointer-events: auto;
      }

      @media (min-width: 769px) {
        position: relative;
        width: 100%;
        height: 450px;
        transform: none !important;
        opacity: 1 !important;
        
        &:hover {
          transform: translateY(-10px) !important;
          box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4), 0 0 15px var(--card-color);
        }
      }

      &.active {
        box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
      }

      .card-image {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        padding: 20px;
      }

      .card-overlay {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 70%;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.2), transparent);
        z-index: 1;
      }
      
      .card-status-overlay {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.7);
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 10;
        
        .status-badge {
          background: rgba(255, 255, 255, 0.9);
          color: #333;
          padding: 8px 16px;
          border-radius: 20px;
          font-weight: bold;
          font-size: 0.9rem;
          text-transform: uppercase;
          letter-spacing: 1px;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
        }
      }

      .card-title {
        color: white;
        font-size: 1.8rem;
        font-weight: bold;
        margin: 0;
        position: relative;
        z-index: 2;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
        font-family: 'TheBoldFont', 'Wendy one', sans-serif;
        letter-spacing: 1px;
      }
    }
  }

  // Dot indicators
  .carousel-dots {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 30px;
    position: relative;
    z-index: 20;

    .dot {
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.4);
      cursor: pointer;
      transition: all 0.3s ease;

      &.active {
        background-color: white;
        transform: scale(1.3);
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.7);
      }
    }
  }
}

// Media query for mobile
@media (max-width: 768px) {
  .capsule-cards-container {
    padding: 0;
    height: 100vh;
    
    .cards-slider {
      height: 80vh;
    }
    
    .carousel-dots {
      margin-top: 20px;
    }
  }
}
