.cards-page-container {
  min-height: 100vh;
  width: 100%;
  max-width: 100vw;
  padding: 2rem 0;
  position: relative;
  overflow-x: hidden;
  overflow-y: visible;
  z-index: 0;
  
  // Prevent horizontal scrolling on mobile
  @media (max-width: 768px) {
    width: 100vw;
    margin: 0;
    padding: 1rem 0;
  }
  
  .cards-page-header {
    text-align: center;
    margin-bottom: 1rem;
    padding: 1.5rem 0 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .page-title {
      font-size: 4rem;
      font-weight: bold;
      margin-bottom: 1rem;
      color: white;
      font-family: 'TheBoldFont', 'Wendy one', sans-serif;
      letter-spacing: 3px;
      text-transform: uppercase;
      position: relative;
      display: block;
      
      span {
        color: #36caba;
        position: relative;
        display: inline-block;
        margin-left: 10px;
        font-family: 'TheBoldFont', 'Wendy one', sans-serif;
      }
      
      @media (max-width: 768px) {
        font-size: 2.5rem;
      }
    }
    
    @keyframes glitchText {
      0% {
        transform: translateX(-3px);
        filter: blur(8px);
      }
      25% {
        transform: translateX(3px);
        filter: blur(4px);
      }
      50% {
        transform: translateX(-2px);
        filter: blur(10px);
      }
      75% {
        transform: translateX(2px);
        filter: blur(6px);
      }
      100% {
        transform: translateX(-3px);
        filter: blur(8px);
      }
    }
    
    .page-subtitle {
      font-size: 1.2rem;
      color: rgba(255, 255, 255, 0.8);
      max-width: 600px;
      margin: 0 auto 2rem;
      font-family: 'TheBoldFont', 'Wendy one', sans-serif;
      letter-spacing: 2px;
      text-transform: uppercase;
      position: relative;
      display: block;
      padding: 0.5rem 2rem;
      background: rgba(15, 15, 30, 0.4);
      border-left: 1px solid rgba(54, 202, 186, 0.5);
      border-right: 1px solid rgba(54, 202, 186, 0.5);
      backdrop-filter: blur(5px);
      clip-path: polygon(0 0, 100% 0, 95% 100%, 5% 100%);
      text-shadow: 0 0 10px rgba(54, 202, 186, 0.5);
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
      
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(90deg, transparent, rgba(54, 202, 186, 0.1), transparent);
        width: 150%;
        transform: translateX(-100%);
        animation: shimmerEffect 4s infinite;
      }
      
      @media (max-width: 768px) {
        font-size: 1rem;
        padding: 0.4rem 1.5rem;
      }
    }
    
    @keyframes shimmerEffect {
      0% {
        transform: translateX(-100%);
      }
      100% {
        transform: translateX(100%);
      }
    }
  }
  
  .capsule-cards-section {
    position: relative;
    z-index: 10;
    margin-top: 0;
    padding: 0;
    pointer-events: auto;
  }
}

@keyframes shimmer {
  0% { background-position: 0% 50%; }
  100% { background-position: 100% 50%; }
}

@keyframes glow {
  0% { filter: drop-shadow(0 0 5px rgba(54, 202, 186, 0.7)); }
  50% { filter: drop-shadow(0 0 20px rgba(54, 202, 186, 0.9)); }
  100% { filter: drop-shadow(0 0 5px rgba(54, 202, 186, 0.7)); }
}
